.contact-container{
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  /* padding: 54px; */

}

.form-container{
    display: flex;
    width:576px;
    height: 308px;
    color: #abf0ff;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #76e5ff29;
}
.form-head{
  color: #62e2ff;

    
}
label{
  position: relative;
  font-weight: bold;
  margin-top: 12px;
}
input{
  border-bottom: 2px solid #4b52e6;
  width: 340px;
}
.first-input{
  width:165px;
  margin-right:10px ;
}
.last-input{
  width:165px;
}

.name-input{
  display: inline-flex;
}
.name-label{
  right:195px;
}
.email-label{
  right:198px;
}
.sub-label{
  right:191px; 
}

select{
  position: relative;
  right:109px;
  margin-bottom: 21px;
  border-bottom: 2px solid #4b52e6;
}
button {
  border-radius: 15px;
  background-color: #99ffe733;
  border: none;
  color: #79f0da;
  padding: 4px 10px;
}
button:hover{
  /* color: #f563fa; */
  font-weight: bold;
}
.sub-head{
  color:#e349eb;
}
.sub-data{
  overflow: auto;
  height: 303px;
  width:576px;
  padding: 11px;
  background-color: #ff00aa36;
  color: #f98fff;

}
.sub-text{
  font-family: 'Times New Roman', Times, Helvetica, sans-serif;
  font-size: 25px;
}
.sub-text-design{
  text-align: center;

}